<template>
  <p> Zugeordnet mit der deutschen Tastatur </p>
  <!-- <template v-for="item in eingabe_list" :key="item"> 
    <span id="showInput">{{ item }}</span>
  </template> -->
  <KeyInteraction v-if="key_interaction" :theme="key_.alph" :count="key_.count" :list="eingabe_list"/>
  <input id="userInput" type="text" v-model="eingabe" @keypress="playSound" onblur="this.focus()" autofocus>
    <!-- <DrumInteraction v-if="drum_interaction"> -->
</template>

<script>
import KeyInteraction from './components/KeyInteraction.vue'

export default {
  name: 'App',
  components: { KeyInteraction },
  data() {
    return {
      key_interaction: false,
      drum_interaction: false,
      eingabe: '',
      key_: { alph: '', count: 0 },
      eingabe_list: [],
      audio_list_drums: [
        { alph: 'a', src: 'https://toreknipping.com/assets/audio/kick1.mp3'},
        { alph: 's', src: 'https://toreknipping.com/assets/audio/snare.mp3'},
        { alph: 'c', src: 'https://toreknipping.com/assets/audio/snap.mp3'},
        { alph: 'd', src: 'https://toreknipping.com/assets/audio/clap_phat.mp3'},
        { alph: 'e', src: 'https://toreknipping.com/assets/audio/hihat2.mp3'},
        { alph: 'q', src: 'https://toreknipping.com/assets/audio/kick_loop.mp3'},
        { alph: 'v', src: 'https://toreknipping.com/assets/audio/vocal.mp3'},
        { alph: 'w', src: 'https://toreknipping.com/assets/audio/hihat.mp3'},
        { alph: 'x', src: 'https://toreknipping.com/assets/audio/clap_phaser2.mp3'},
        { alph: 'y', src: 'https://toreknipping.com/assets/audio/clap_phaser.mp3'},        
      ],
      audio_list_keys: [
        { alph: 'r', src: 'https://toreknipping.com/assets/audio/key_a1.mp3', count: 0},
        { alph: 't', src: 'https://toreknipping.com/assets/audio/key_a2.mp3', count: 0},
        { alph: 'z', src: 'https://toreknipping.com/assets/audio/key_a3.mp3', count: 0},
        { alph: 'u', src: 'https://toreknipping.com/assets/audio/key_a4.mp3', count: 0},
        { alph: 'i', src: 'https://toreknipping.com/assets/audio/key_a5.mp3', count: 0},
        { alph: 'o', src: 'https://toreknipping.com/assets/audio/key_a6.mp3', count: 0},
        { alph: 'p', src: 'https://toreknipping.com/assets/audio/key_a7.mp3', count: 0},
        { alph: 'ü', src: 'https://toreknipping.com/assets/audio/key_1n.mp3', count: 0},
        { alph: '+', src: 'https://toreknipping.com/assets/audio/key_2n.mp3', count: 0},
        { alph: '#', src: 'https://toreknipping.com/assets/audio/key_3n.mp3', count: 0},
        { alph: 'f', src: 'https://toreknipping.com/assets/audio/key_b1.mp3', count: 0},
        { alph: 'g', src: 'https://toreknipping.com/assets/audio/key_b2.mp3', count: 0},
        { alph: 'h', src: 'https://toreknipping.com/assets/audio/key_b3.mp3', count: 0},
        { alph: 'j', src: 'https://toreknipping.com/assets/audio/key_b3_1.mp3', count: 0},
        { alph: 'k', src: 'https://toreknipping.com/assets/audio/key_b4.mp3', count: 0},
        { alph: 'l', src: 'https://toreknipping.com/assets/audio/key_b5.mp3', count: 0},
        { alph: 'ö', src: 'https://toreknipping.com/assets/audio/key_b6.mp3', count: 0},
        { alph: 'ä', src: 'https://toreknipping.com/assets/audio/key_b7.mp3', count: 0},
        { alph: 'b', src: 'https://toreknipping.com/assets/audio/key_b8.mp3', count: 0},
        { alph: 'n', src: 'https://toreknipping.com/assets/audio/key_b9.mp3', count: 0},
        { alph: 'm', src: 'https://toreknipping.com/assets/audio/key_b10.mp3', count: 0},
      ],
    }
  },
  methods: {
    playSound(e) {
      var user_input = e.key.toUpperCase()

      var index = this.audio_list_keys.map(function(element) {return element.alph.toUpperCase()}).indexOf(user_input)
      if (index != -1) {
        this.key_.alph = user_input
        this.key_interaction = true  
        this.eingabe_list.push(this.key_.alph)

        this.audio_list_keys[index].count++
        this.key_.count = this.audio_list_keys[index].count

        // console.log(this.audio_list_keys[index].alph + ': '+ this.audio_list_keys[index].count)
        var audio = new Audio(this.audio_list_keys[index].src)
        // audio.addEventListener('loadeddata', () => {
        //   this.duration = audio.duration * 1000
        //   console.log(this.duration)
        // })
        // setTimeout(function() {
        //   this.showInteraction = false
        // }, this.duration)        
        audio.play()
        this.eingabe = ''
      } else {
        var index = this.audio_list_drums.map(function(element) {return element.alph.toUpperCase()}).indexOf(user_input)
        if(index != -1) {
          this.drum_interaction = true
          this.eingabe_list.push(user_input)
          var audio = new Audio(this.audio_list_drums[index].src)
          audio.play()
          this.eingabe = ''
        } else {
          this.eingabe = ''
        }
      } 
    }
  },
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: white;
  margin-top: 60px;
}

body {
  background-color: black;
  color: white;
}

#userInput {
  background-color: black;
  width: 100%;
  box-sizing: content-box;
  border: none;
  resize: none;
  text-align: start;
  margin: 20px;
}

*:focus {
  outline: none !important;
}

#showInput {
  font-size: 30px;
  margin: 2px;
  width: 70%;
}

</style>
